.form{
    max-width: 1200px;
    margin: 0 auto;
    padding-top: 206px;
    display: flex;

    &__inputText{
        max-width: 83%;
        width: 100%;
        border: 1px solid #1AA6B7;
        color: #1AA6B7;
        font-weight: bold;
        border-radius: 10px;
        font-size: 30px;
        line-height: 37px;
        font-family: Lexend;
        padding: 6px 0 6px 20px;
    }
    &__inputText::placeholder{
        font-style: normal;
        font-weight: bold;
        font-size: 30px;
        line-height: 37px;
        color:#1AA6B7;
        

    }
    &__btn{
        max-width: 15%;
        width: 100%;
        margin-left: 2%;
        background: #1AA6B7;
        border-radius: 10px;
        padding: 10px;
        font-weight: bold;
        font-size: 20px;
        line-height: 25px;
        font-family: Lexend;
        color: #F6F6F6;
    }
}